import Vue from "vue";
import Router from 'vue-router'

import Home from '@/views/home.vue'

Vue.use(Router);

// 使用$router.push跳转到一个相同的路由,解决办法
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

const router = new Router({
	mode: 'hash',
	base: process.env.VUE_APP_SRC,
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/logs',
			name: 'logs',
			component: () => import('@/views/logs.vue')
		},
		// Git
		{
			path: '/Git/GitCommon',
			name: 'GitCommon',
			component: () => import('@/views/Git/GitCommon.vue')
		},
		{
			path: '/Git/GitOther',
			name: 'GitOther',
			component: () => import('@/views/Git/GitOther.vue')
		},
		{
			path: '/Git/GitPractice',
			name: 'GitPractice',
			component: () => import('@/views/Git/GitPractice.vue')
		},
	],
});

export default router;