<template>
  <div class="leftSide">
    <p class="leftSide-title leftSide-logs" :style="logsChoose === true ? 'color: #0095ff' : ''" @click="$router.push('/logs')" style="cursor: pointer;">更新日志</p>
    <p class="leftSide-title">Git</p>
    <el-tree :data="leftSideData" :props="defaultLeftSideData" @node-click="handleNodeClick" node-key="id"></el-tree>
  </div>
</template>

<script>
import { leftSideData } from '../utils/leftSideData.js'

export default {
  data() {
    return {
      logsChoose: false,
      leftSideData: [],
      defaultLeftSideData: {
        children: 'children',
        label: 'label'
      },
    };
  },

  watch: {
    $route: {
      handler(route) {
        if (route.path === '/logs') {
          this.logsChoose = true;
          console.log(this.logsChoose);
        } else {
          this.logsChoose = false;
        }
        // if (route.query.expanded) {

        // }
        // if (route.query.checked) {

        // }
      },
      immediate: true,
      deep: true
    }
  },

  created() {
    this.leftSideData = leftSideData; // eslint-disable-line no-unused-vars
  },

  methods: {
    handleNodeClick(data) {
      if (data.link) {
        this.$router.push(data.link)
      }
      // console.log(data);
    },
  },
}
</script>

<style lang="scss">
.leftSide {
  position: fixed;
  z-index: 999;
  width: 300px;
  height: 100%;
  border-right: 1px solid #dcdfe6;
  padding-left: 5px;
  .leftSide-title {
    padding: 10px;
    font-size: 1.4em;
    font-weight: 700;
    user-select: none;
  }
  .leftSide-logs {
    transition: all 0.5s ease;
  }
  .leftSide-logs:hover {
    color: $text-blue;
  }
}
</style>